.App-welcome {
  display: grid;
  min-height: 96vh;
  max-height: 550px;
}

.intro {
  align-self: end;
}

.intro h1 {
  margin-bottom: 0;
}

.bottomNav {
  padding: 0;
  align-self: end;

  display: flex;
  align-items: end;
  justify-content: space-between;
}
.bottomNav li {
  list-style: none;
}
.scrollIcon {
  display: block;
  color: rgba(50, 50, 50, 0.9);
  border: 1px solid rgba(50, 50, 50, 0.4);
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}
.scrollIcon:hover {
  color: rgba(50, 50, 50, 1);
  border: 1px solid rgba(50, 50, 50, 0.6);
}

.links {
  display: flex;
  width: 65px;
  justify-content: space-evenly;
}
